<template>
<div class="wrapper-bg">
  <div class="wrapper">
    <div class="content">
      <div class='c-top'>
        <div class='c-t-left flex-1'>
          <ctitle :title="'区划分布'"></ctitle>
          <div class="charts">
            <barline :params="xflphBarParams"></barline>
          </div>
        </div>
        <div class='c-t-left flex-2'>
          <ctitle :title="'集体访信访量走势'"></ctitle>
          <div class="charts">
            <LinesR :params="zsLineParams"></LinesR>
          </div>
        </div>
      </div>
      <div class='c-bottom'>
        <div class='c-b-left'>
          <ctitle :title="'人数规模分布'"></ctitle>
          <div class="charts">
            <loopPieNew :params="peoplePieParams"></loopPieNew>
          </div>
        </div>
        <div class='c-b-left'>
          <ctitle :title="'信访群体'"></ctitle>
          <div class="charts">
            <barline :params="rqnlBarParams"></barline>
          </div>
          <!-- <p>年龄分布</p> -->
        </div>
        <div class="c-b-left">
          <ctitle :title="'信访诉求'"></ctitle>
          <div class="charts chart-pie">
            <pie-chart3D :params="xfmdPieParams"></pie-chart3D>
          </div>
          <p>信访问题分布</p>
        </div>
        <div class='c-b-left'>
          <!-- <ctitle :title="'投诉问题分布'"></ctitle> -->
          <div class="charts">
            <loopPie :params="tswtPieParams"></loopPie>
          </div>
          <p>内容分类</p>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  // import barlineY from "@/components/bar/barLine2";
  import barline from "@/components/bar/BarLine";
  import LinesR from "@/components/line/LinesR";
  import loopPie from "@/components/pie/LoopPie";
  import PieChart3D from "@/components/pie/PieChart3D";
  import loopPieNew from "@/components/pie/LoopPie1";
  
  export default {
    name: "jtf",
    components: {
      ctitle,
      barline,
      LinesR,
      loopPie,
      PieChart3D,
      // barlineY,
      loopPieNew
    },
    data(){
      return{
        // 1.区划分布
        xjName:[],
        // curCity: "云南省",
        // shijCity: "",
        // xjCity: "",
        // shej_02: "云南省",
        // shij_02: "invalid",
        // xj_02: "invalid",
        // nameP:'invalid',
        xflphBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            // isVertical:true,
            xName: "name", //x轴字段
            axisValueShow: false,
            categoryBr: true,
            axis1Name: "人次",
            axisLabelSize: 14, // label字号
            left: "10%",
            right: "10%",
            top: "5%",
            bottom: "5%",
            barLabelOptions: "top",
            barWidth: "25%",
            barBorderRadius: 5,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#fa71cd", "#c471f5"]]
            },
            map: {
              value: "数量"
            }
          }
        },
        // 2.集体访信访量走势
        zsLineParams: {
          data: [],
          colors: [
            // 紫
            {
							line: "rgba(14,149,210, 1)",
							start: "rgba(14,149,210, 0.5)",
							end: "rgba(14,149,210, 0.5)"
						},
          ],
          option: {
            YaxisLineShow: true,
            lineWidth: '0',
            splitNumber: 4,
            splitLineShow: false,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            // axisYName: "件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: .55,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "5%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "10%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            value: "数量"
          },
          xName: "name"
        },
        // 3.人数规模分布
        peoplePieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            borderColor: '#050e27',
						borderWidth: 7,
						minAngle: 15,
            // colors: this.$STANDARD.pieMoreColors,
            colors: ['#ffc833', '#eb9002', '#1ba1ff', '#4eed99', '#13f0ff'],
            // colors: [],
            // radius: ["30%", "65%"],
            radius: ["35%", "51%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            tooltip: {
              show: true
            },
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 5,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true,
          }
        },
        // 4.信访群体
        rqnlBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "95%"
          },
          options: {
            yAxisColor: '#fff',
            xName: "name", //x轴字段
            axisValueShow: true,
            axis1Name:"",
            axisLabelSize: "12%", // label字号
            left: "10%",
            right: "10%",
            top: "10%",
            bottom: "15%",
            barLabelOptions: "top",
            // barWidth: 30,
            barWidth: "45%", // 每个柱形图宽
            splitLineShow: false,
            barLabelShow: true, //柱状图数量值显示
            itemLabelColor: "#cdefff",

            // axisLabelColor: "rgba(0, 176, 255, .8)",
            // itemLabelColor: "rgba(0, 176, 255, .8)",
            // barGap: '30%',// 柱间距离，
            colors: {
              bar: [["#D5FA9E", "#9ADE72"]]
            },
            map: {
              value: "人数"
            },
          }
        },
        // 5.信访诉求
        xfmdPieParams: {
          data: [],
          angle: 30,
          radius: "28%",
          labelRadius: 30,
          depth3D: 25,
          fontSize: this.fontSize(0.16),
          color:  ['#d93e75', '#ffc833', '#4eed99', '#13f0ff', '#1ba1ff'],
          styleObj: {
            width: "100%",
            height: "100%"
          }
        },
        // 6.内容分类
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            // colors: this.$STANDARD.pieColors,
            colors: ['#4eed99', '#d93e75', '#ccbe51', '#eb9002', '#1ba1ff', '#563ad2', '#f89e67', '#13f0ff'],
            radius: ["36%", "45%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.15),
              d: this.fontSize(0.18),
              bHeight: this.fontSize(0.2)
            },
            length: 20,
            length2: 50,
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 4,
            borderColor: "#050e27",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            tooltip: {
              show: false 
            },
            rich: {
              bfontsize: "10%" //文字大小
            },
            labelSize: "22%", // label字号
            threeLevel:true,
            rollFlag: true
          }
        },
      }
    },
    mounted() {
      this.getAPI(this.date)
    },
    methods:{

      // 接口调试 
      getAPI(date){
        // 1.区划分布
        get_http("rdmxfxqtjjfxfldqfb", {
          day: `[${date.startDate},${date.endDate}]`,
        })
          .then(res => {
            this.xflphBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.集体访信访量走势
        get_http("rdmxfxjtffbzs", {
          // data: data,
          // xfxs: id,
          // shej_02:this.shej_02,
          // shij_02:this.shij_02,
          // xi_02:this.xi_02,
          day: `[${date.startDate},${date.endDate}]`,
          })
          .then(res => {
            // this.zsLineParams.option.axisCategoryName =
            //   time === "年度" ? "月" : "月";
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.人数规模分布
        get_http("rdmxfxjjfrsgmfb", {
          day: `[${date.startDate},${date.endDate}]`,
          }).then(res => {
            this.peoplePieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4.信访群体
        get_http("rdmxfxjtfxfqt", {
          day: `[${date.startDate},${date.endDate}]`,
          }).then(res => {
            this.rqnlBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.信访目的分布
        get_http("rdmxfxjtfxfsq", {
          day: `[${date.startDate},${date.endDate}]`,
          }).then(res => {
            this.xfmdPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 6.内容分类
        get_http("rdmxfxjtftswtfb", {
          day: `[${date.startDate},${date.endDate}]`,
          })
          .then(res => {
            this.tswtPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val, oval) {
        if (oval.startDate || oval.endDate) {
          this.getAPI(val);
        }
      },
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper-bg
  background url("../../../assets/wrapper-bg2.png") no-repeat center
  background-size 100% 100%
  width 100%
  height 100%
  .wrapper
    position relative
    top 0.45rem
    height calc(100% - 0.45rem)
    .content
      display flex
      flex-direction column
      height 100%
      padding .2rem .3rem
      box-sizing border-box
      .c-top
        flex 1
        display flex
        margin-bottom .3rem
        .c-t-left
          flex 1  
          display flex
          flex-direction column
          position relative
          .map-back
            position absolute
            top 30px
            right 60px
            width 30px
            height 30px
            background url("~@/assets/back-icon.png") no-repeat
            z-index 1000
            font-size 0.2rem
            color #fff
            cursor pointer
          .charts
            flex 1
        .flex-2
          flex 2
        .flex-1
          flex 1.5
      .c-bottom
        flex 1
        display flex
        .c-b-left
          flex 1  
          display flex
          flex-direction column
          position relative
          .charts
            flex 1
          p
            width 100%
            position absolute
            bottom .3rem
            right 0px
            color #fff
            font-size 20px
            text-align center
            font-weight bold
      
      
</style>
